@use "./abstract/" as *;
@use "./modules/variables" as v;
/* footer */

.footer {
  margin-top: auto;
  padding-top: 40px;
  width: 100%;
  min-height: 350px;

  display: flex;
  flex-direction: column;
  background: $footer-gradient-background;
}

.footer__wrapper {
  padding-inline: v.$wrapper-inline;
  display: flex;
  justify-content: flex-start;
  gap: 3.5%;

}

.titleH2--footer {
  margin-top: 16px;
  margin-bottom: 32px;
  color: $color-light-xl;
  text-align: left;
}

.contacts__item:not(:last-child) {
  margin-bottom: 34px;
}

.footer__contacts,
.footer__address {
  flex-basis: 33%;
}

.footer__address {
  max-width: 302px;
}

.contacts__link {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  align-items: center;
}

.address__link::before {
  content: '';
  width: 22px;
  height: 32px;
  position: absolute;
  background: url('../img/icons/pin.svg');
}

.address__link h3{
padding-left: 43px;
}

.contacts__info {
  font-size: 1.25rem;
  color: $color-primary;
  font-weight: 400;
  line-height: 115%;
  letter-spacing: 0.06em;

}

.footer__img {
  margin-left: auto;

  max-width: 300px;
  width: 100%;
  height: 310px;

  background: url('../img/bg/footer-puppy.png');
  background-repeat: no-repeat;
  background-size: contain;
}
